import React, { useEffect, useState } from "react";
import { ORDERS_PER_PAGE } from "../../../app/constants";
import { fetchAllOrderAsync, updateOrderAsync } from "../../order/orderSlice";
import { useDispatch, useSelector } from "react-redux";
import {
  XMarkIcon,
  EyeIcon,
  PencilIcon,
  ChevronRightIcon,
  ChevronLeftIcon,
  ArrowUpIcon,
  ArrowDownIcon,
} from "@heroicons/react/24/outline";

function Pagination({ page, handlePage, totalOrders }) {
  return (
    <div className="flex items-center justify-between border-t border-gray-200 bg-white px-4 py-3 sm:px-6">
      <div className="flex flex-1 justify-between sm:hidden">
        <div
          onClick={(e) => handlePage(e, Math.max(1, page - 1))}
          className="relative inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Previous
        </div>
        <div
          onClick={(e) =>
            handlePage(
              e,
              Math.min(page + 1, Math.ceil(totalOrders / ORDERS_PER_PAGE))
            )
          }
          className="relative ml-3 inline-flex items-center rounded-md border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
        >
          Next
        </div>
      </div>
      <div className="hidden sm:flex sm:flex-1 sm:items-center sm:justify-between">
        <div>
          <p className="text-sm text-gray-700">
            Showing{" "}
            <span className="font-medium">
              {(page - 1) * ORDERS_PER_PAGE + 1}
            </span>{" "}
            to{" "}
            <span className="font-medium">
              {page * ORDERS_PER_PAGE > totalOrders
                ? totalOrders
                : page * ORDERS_PER_PAGE}
            </span>{" "}
            of <span className="font-medium">{totalOrders}</span> results
          </p>
        </div>
        <div>
          <nav
            className="isolate inline-flex -space-x-px rounded-md shadow-sm"
            aria-label="Pagination"
          >
            <div
              onClick={(e) => handlePage(e, Math.max(1, page - 1))}
              className="relative inline-flex items-center rounded-l-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
            >
              <span className="sr-only">Previous</span>
              <ChevronLeftIcon className="h-5 w-5" aria-hidden="true" />
            </div>
            {/* Current: "z-10 bg-indigo-600 text-white focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600", Default: "text-gray-900 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:outline-offset-0" */}

            {Array.from({
              length: Math.ceil(totalOrders / ORDERS_PER_PAGE),
            }).map((el, index) => (
              <div
                onClick={(e) => handlePage(e, index + 1)}
                divria-current="page"
                className={`relative cursor-pointer z-10 inline-flex items-center ${
                  index + 1 === page
                    ? "bg-indigo-600 text-white"
                    : "text-gray-400"
                } px-4 py-2 text-sm font-semibold  focus:z-20 focus-visible:outline focus-visible:outline-2 focus-visible:outline-offset-2 focus-visible:outline-indigo-600`}
              >
                {index + 1}
              </div>
            ))}

            <div
              onClick={(e) =>
                handlePage(
                  e,
                  Math.min(page + 1, Math.ceil(totalOrders / ORDERS_PER_PAGE))
                )
              }
              className="relative inline-flex items-center rounded-r-md px-2 py-2 text-gray-400 ring-1 ring-inset ring-gray-300 hover:bg-gray-50 focus:z-20 focus:outline-offset-0"
            >
              <span className="sr-only">Next</span>
              <ChevronRightIcon className="h-5 w-5" aria-hidden="true" />
            </div>
          </nav>
        </div>
      </div>
    </div>
  );
}

const AdminOrders = () => {
  const dispatch = useDispatch();

  const [selectEditOrder, setSelectEditOrder] = useState(-1);
  const [page, setPage] = useState(1);
  const [sort, setSort] = useState({ _sort: "orderId", _order: "asc" });
  const orders = useSelector((state) => state.order.orders);
  const totalOrders = useSelector((state) => state.order.totalOrders);

  const chooseColor = (status) => {
    switch (status) {
      case "Pending":
        return "text-purple-600 bg-purple-200";
      case "Dispatched":
        return "text-yellow-600 bg-yellow-200";
      case "Delivered":
        return "text-green-600 bg-green-200";
      case "Cancelled":
        return "text-red-600 bg-red-200";
      default:
        return "text-purple-600 bg-purple-200";
    }
  };

  const handleEdit = (order) => {
    setSelectEditOrder(order.orderId);
  };

  const handleShow = (order) => {
    console.log(order);
  };

  const handleUpdate = (e, order) => {
    const updatedOrder = { ...order, status: e.target.value };
    dispatch(updateOrderAsync(updatedOrder));
    setSelectEditOrder(-1);
  };

  const handleSort = (option) => {
    if (sort._sort === option) {
      const sorting = { _sort: option, _order: "asc" };
      if (sort._order === "asc") {
        sorting._order = "desc";
      }
      setSort(sorting);
    } else {
      const sorting = { _sort: option, _order: "asc" };
      setSort(sorting);
    }
  };

  const handlePage = (e, page) => {
    setPage(page);
  };

  useEffect(() => {
    const pagination = { _page: page, _limit: ORDERS_PER_PAGE };
    dispatch(fetchAllOrderAsync({ sort, pagination }));
  }, [dispatch, page, sort]);

  return (
    <div className="overflow-x-auto">
      <div className=" flex items-center justify-center font-sans overflow-hidden">
        <div className="w-full">
          <div className="bg-white shadow-md rounded my-6">
            <table className="min-w-max w-full table-auto">
              <thead>
                <tr className="bg-gray-200 text-gray-600 uppercase text-sm leading-normal">
                  <th
                    className="py-3 px-6 text-left cursor-pointer"
                    onClick={(e) => handleSort("orderId")}
                  >
                    Order Id &nbsp;
                    {(sort._sort === "orderId") && (sort._sort === "orderId" && sort._order === "asc" ? (
                      <ArrowUpIcon className="w-4 h-4 inline" />
                    ) : (
                      <ArrowDownIcon className="w-4 h-4 inline" />
                    ))}
                  </th>
                  <th
                    className="py-3 px-6 text-left"
                  >
                    Items
                  </th>
                  <th
                    className="py-3 px-6 text-center cursor-pointer"
                    onClick={(e) => handleSort("totalAmount")}
                  >
                    Total Amount &nbsp;
                    {(sort._sort === "totalAmount") && (sort._sort === "totalAmount" && sort._order === "asc" ? (
                      <ArrowUpIcon className="w-4 h-4 inline" />
                    ) : (
                      <ArrowDownIcon className="w-4 h-4 inline" />
                    ))}
                  </th>
                  <th
                    className="py-3 px-6 text-center"
                  >
                    Shipping Address
                  </th>
                  <th
                    className="py-3 px-6 text-center cursor-pointer"
                    onClick={(e) => handleSort("status")}
                  >
                    Status &nbsp;
                    {(sort._sort === "status") && (sort._sort === "status" && sort._order === "asc" ? (
                      <ArrowUpIcon className="w-4 h-4 inline" />
                    ) : (
                      <ArrowDownIcon className="w-4 h-4 inline" />
                    ))}
                  </th>
                  <th
                    className="py-3 px-6 text-center cursor-pointer"
                  >
                    Actions
                  </th>
                </tr>
              </thead>
              <tbody className="text-gray-600 text-sm font-light">
                {orders.map((order) => (
                  <tr className="border-b border-gray-200 hover:bg-gray-100">
                    <td className="py-3 px-6 text-left whitespace-nowrap">
                      <div className="flex items-center">
                        <span className="font-medium">{order.orderId}</span>
                      </div>
                    </td>
                    <td className="py-3 px-6 text-left">
                      {order.items.map((item) => (
                        <div className="flex items-center">
                          <div className="mr-2">
                            <img
                              className="w-12  h-12 rounded-full"
                              src={item.product.thumbnail}
                            />
                          </div>
                          <span>
                            {item.product.title}&nbsp;&nbsp;x {item.quantity}
                          </span>
                        </div>
                      ))}
                    </td>
                    <td className="py-3 px-6 text-center">
                      <div className="flex items-center justify-center">
                        ${order.totalAmount}
                      </div>
                    </td>
                    <td className="py-3 px-6 text-center">
                      <div className="items-center justify-center">
                        <div>
                          <strong>{order.shippingAddress.name}</strong> -{" "}
                          {order.shippingAddress.phone}
                        </div>
                        <div>{order.shippingAddress.street},</div>
                        <div>{order.shippingAddress.city},</div>
                        <div>{order.shippingAddress.state},</div>
                        <div>{order.shippingAddress.pincode}</div>
                      </div>
                    </td>

                    <td className="py-3 px-6 text-center">
                      {selectEditOrder === order.orderId ? (
                        <select
                          value={order.status}
                          onChange={(e) => handleUpdate(e, order)}
                        >
                          <option value="Pending">Pending</option>
                          <option value="Dispatched">Dispatched</option>
                          <option value="Delivered">Delivered</option>
                          <option value="Cancelled">Cancelled</option>
                        </select>
                      ) : (
                        <span
                          className={`${chooseColor(
                            order.status
                          )} py-1 px-3 rounded-full text-xs`}
                        >
                          {order.status}
                        </span>
                      )}
                    </td>
                    <td className="py-3 px-6 text-center">
                      <div className="flex item-center justify-center">
                        <div className="w-6 mr-4 transform hover:text-purple-500 hover:scale-125">
                          <EyeIcon
                            className="w-6 h-6"
                            onClick={(e) => handleShow(order)}
                          />
                        </div>
                        <div className="w-6 mr-2 transform hover:text-purple-500 hover:scale-125">
                          <PencilIcon
                            className="w-6 h-6"
                            onClick={(e) => handleEdit(order)}
                          />
                        </div>
                      </div>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>
      </div>
      <Pagination
        page={page}
        handlePage={handlePage}
        totalOrders={totalOrders}
      ></Pagination>
    </div>
  );
};

export default AdminOrders;
