import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { addToCart, deleteCart, fetchCount, fetchItemsByUser, resetCart, updateCart } from './cartAPI';

const initialState = {
  value: 0,
  items: [],
};

// export const incrementAsync = createAsyncThunk(
//   'counter/fetchCount',
//   async (amount) => {
//     const response = await fetchCount(amount);

//     return response.data;
//   }
// );

export const addToCartAsync = createAsyncThunk(
  'cart/addToCart',
  async (item) => {
    const response = await addToCart(item);

    return response.data;
  }
);

export const fetchItemsByUserAsync = createAsyncThunk(
  'cart/fetchItemsBy',
  async () => {
    const response = await fetchItemsByUser();
    return response.data;
  }
);

export const updateCartAsync = createAsyncThunk(
  'cart/updateCart',
  async (updateItem) => {
    const response = await updateCart(updateItem);
    return response.data;
  }
);

export const deleteCartAsync = createAsyncThunk(
  'cart/deleteCart',
  async (itemId) => {
    const response = await deleteCart(itemId);
    return response.data;
  }
);

export const resetCartAsync = createAsyncThunk(
  'cart/resetCart',
  async () => {
    const response = await resetCart();
    return response.data;
  }
);

export const cartSlice = createSlice({
  name: 'cart',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    increment: (state) => {
      state.value += 1;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(addToCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(addToCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items.push(action.payload);
      })
      .addCase(fetchItemsByUserAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(fetchItemsByUserAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items = (action.payload); 
      })
      .addCase(updateCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(updateCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index =  state.items.findIndex((item) => item.id === action.payload.id)
        state.items[index].quantity = action.payload.quantity; 
      })
      .addCase(deleteCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(deleteCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        const index =  state.items.findIndex((item) => item.id === action.payload.id)
        state.items.splice(index,1);
      })
      .addCase(resetCartAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(resetCartAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.items = [];
      })
  },
});

export const { increment } = cartSlice.actions;

export default cartSlice.reducer;
